import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import PdfView from '../views/PDFView.vue'

Vue.use(VueRouter)

const routes= [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/pdf',
    name: 'pdfView',
    component: PdfView
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
