<template>
<div>
    
    <div style="margin:2.5% 5% 0 5%;width:90%;box-shadow:10px 5px 5px;border:2px solid black;" v-for="i in 6" :key=i>
        <pdf ref="pdf" src='paper.pdf' :page="i"></pdf>
    </div>
    
</div>
</template>

<script>
import pdf from 'vue-pdf';
export default {
    name: "PdfView",
    components: {
        pdf,
    },
    data(){
        return{
            url: '',
            numPages:1,
            publicPath:process.env.BASE_URL
        }
    },
    created: function() {
        this.getNumPages(`${this.publicPath}/paper.pdf`)
    }, 
    methods: {
        getNumPages(url) {
            var loadingTask = pdf.createLoadingTask(url)
            loadingTask.then(pdfObj => {
                this.url = loadingTask
                this.numPages = pdfObj.numPages
            }).catch((err) => {
                console.error(err)
            })
        },
    }
}
</script>
